@charset "UTF-8";
/***
    The new CSS reset - version 1.8.4 (last updated 14.2.2023)
    GitHub page: https://github.com/elad2412/the-new-css-reset
***/
/*
    Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property
    - The "symbol *" part is to solve Firefox SVG sprite bug
 */
*:where(:not(html, iframe, canvas, img, svg, video, audio):not(svg *, symbol *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Reapply the pointer cursor for anchor tags */
a, button {
  cursor: revert;
}

/* Remove list styles (bullets/numbers) */
ol, ul, menu {
  list-style: none;
}

/* For images to not be able to exceed their container */
img {
  max-inline-size: 100%;
  max-block-size: 100%;
}

/* removes spacing between cells in tables */
table {
  border-collapse: collapse;
}

/* Safari - solving issue when using user-select:none on the <body> text input doesn't working */
input, textarea {
  -webkit-user-select: auto;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
  white-space: revert;
}

/* minimum style to allow to style meter element */
meter {
  -webkit-appearance: revert;
  appearance: revert;
}

/* preformatted text - use only for this feature */
:where(pre) {
  all: revert;
}

/* reset default text opacity of input placeholder */
::placeholder {
  color: unset;
}

/* remove default dot (â€¢) sign */
::marker {
  content: initial;
}

/* fix the feature of 'hidden' attribute.
   display:revert; revert to element instead of attribute */
:where([hidden]) {
  display: none;
}

/* revert for bug in Chromium browsers
   - fix for the content editable attribute will work properly.
   - webkit-user-select: auto; added for Safari in case of using user-select:none on wrapper element*/
:where([contenteditable]:not([contenteditable=false])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}

/* apply back the draggable feature - exist only in Chromium and Safari */
:where([draggable=true]) {
  -webkit-user-drag: element;
}

/* Revert Modal native behavior */
:where(dialog:modal) {
  all: revert;
}

.btn, .btn-external {
  display: block;
  background: #0F131C;
  width: 320px;
  color: #fff;
  padding: 16px;
  border-radius: 50px;
  transform: translateY(0px);
  box-shadow: 0px 3px 5px rgba(15, 19, 28, 0.2);
  transition: background 0.3s ease, transform 0.3s ease, box-shadow 0s ease 0.3s;
}
@media screen and (max-width: 767px) {
  .btn, .btn-external {
    width: 300px;
  }
}
.btn:hover, .btn-external:hover {
  transform: translateY(1px);
  background-color: #BBC0C9;
}
.btn-external {
  text-align: center;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  min-height: 100%;
  background-color: #fefefe;
  font-size: 16px;
  color: #0F131C;
  font-family: "Inter", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", "BIZ UDPGothic", Meiryo, sans-serif;
  letter-spacing: 0.1ex;
}

img {
  width: 100%;
  object-fit: contain;
}

.container {
  position: relative;
  top: 0;
  z-index: 1;
}

.side-border {
  position: fixed;
  z-index: -1;
  top: 0;
  width: 100%;
  height: 100lvh;
}
.side-border_left {
  display: block;
  position: absolute;
  top: 0;
  left: 40px;
  width: 1px;
  height: 100vh;
  background-color: #BBC0C9;
}
@media screen and (max-width: 767px) {
  .side-border_left {
    left: 20px;
  }
}
.side-border_right {
  display: block;
  position: absolute;
  top: 0;
  right: 40px;
  width: 1px;
  height: 100vh;
  background-color: #BBC0C9;
}
@media screen and (max-width: 767px) {
  .side-border_right {
    right: 20px;
  }
}

.header {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  height: auto;
  background: #fff;
  border-top: 1px solid #0F131C;
  border-bottom: 1px solid #0F131C;
}
.header-inner {
  position: relative;
  margin: 0 40px;
  border-right: 1px solid #0F131C;
  border-left: 1px solid #0F131C;
}
@media screen and (max-width: 767px) {
  .header-inner {
    margin: 0 20px;
    padding: 24px;
  }
}
.header-column {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .header-column {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
.header-logo {
  width: 242px;
  padding: 0 0 0 40px;
}
@media screen and (max-width: 767px) {
  .header-logo {
    width: 160px;
    padding: unset;
  }
}
.header-logo svg {
  width: 100%;
}
.header-toggle {
  display: none;
}
@media screen and (max-width: 767px) {
  .header-toggle {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 16px;
    margin: auto;
    width: 67px;
    height: 7px;
    cursor: pointer;
  }
  .header-toggle > span {
    display: block;
    height: 1px;
    background: #0F131C;
    transition: transform 0.3s ease 0s;
  }
}
.header-navi {
  position: relative;
  display: flex;
  justify-content: flex-start;
  background: #fff;
}
@media screen and (max-width: 767px) {
  .header-navi {
    visibility: hidden;
    position: fixed;
    z-index: 3;
    top: 70px;
    width: 100%;
    height: 100lvh;
    left: 0;
    right: 0;
    margin: auto;
    flex-direction: column;
  }
  .header-navi::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 20px;
    width: 1px;
    height: 100vh;
    background-color: #0F131C;
  }
  .header-navi::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 20px;
    width: 1px;
    height: 100vh;
    background-color: #0F131C;
  }
}
.header-sns {
  display: grid;
  place-content: center;
  border-left: solid 1px #0F131C;
  width: 106px;
  margin: 0 0 0 40px;
}
@media screen and (max-width: 767px) {
  .header-sns {
    width: 89%;
    margin: 40px auto;
    padding: 24px 20px;
    border-top: solid 1px #0F131C;
    border-bottom: solid 1px #0F131C;
    border-left: none;
  }
}
.header-sns a {
  display: block;
  width: 28px;
  height: 28px;
}
.header-sns a:hover {
  opacity: 0.7;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
    filter: blur(0.8px);
  }
  to {
    opacity: 1;
    transform: scale(1);
    filter: blur(0px);
  }
}
#nav-input {
  display: none;
}
#nav-input:checked ~ .header-navi {
  visibility: visible;
  animation: fadeIn 0.3s ease 0s alternate;
}
#nav-input:checked + .header-toggle span:first-child {
  transform: translateY(3px) rotate(14deg);
}
#nav-input:checked + .header-toggle span:last-child {
  transform: translateY(-3px) rotate(-14deg);
}

.navi {
  display: flex;
  gap: 40px;
  padding: 30px 20px;
}
@media screen and (max-width: 767px) {
  .navi {
    flex-direction: column;
    justify-content: center;
  }
}
.navi-item {
  text-align: center;
  position: relative;
}
.navi-item a {
  display: block;
  opacity: 1;
}
.navi-item a::before {
  content: "";
  position: absolute;
  bottom: -16px;
  display: block;
  width: 100%;
  height: 3px;
  transform: scale(0);
  background-color: #0F131C;
  transition: transform 0.3s ease;
}
.navi-item a:hover {
  opacity: 0.7;
}
.navi-item a:hover::before {
  transform: scale(1);
}
.navi-item_en {
  margin: 0 0 8px 0;
  font-size: 18px;
}
.navi-item_ja {
  font-size: 14px;
}

.main-visual {
  width: 100%;
  max-width: 1260px;
  margin: 164px auto 154px;
}
@media screen and (max-width: 767px) {
  .main-visual {
    padding: 0 40px;
    margin: 16vh auto 100px;
  }
}
.main-visual_photo {
  transform: scale(1);
}
.main-visual_logo {
  max-width: 640px;
  margin: 0 auto 56px;
}
@media screen and (max-width: 767px) {
  .main-visual_logo {
    margin: 0 auto 16px;
    width: 65%;
  }
}

.section-titles {
  width: fit-content;
  opacity: 0;
  transition: opacity 0.5s ease 0.8s;
}
.section-titles_head {
  font-weight: 600;
  font-size: 56px;
  color: #fff;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.4);
}
@media screen and (max-width: 767px) {
  .section-titles_head {
    font-size: 24px;
  }
}
.section-titles_sub {
  font-size: 32px;
  font-weight: bold;
  color: #fff;
  letter-spacing: 0.3ex;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.4);
}
@media screen and (max-width: 767px) {
  .section-titles_sub {
    font-size: 16px;
  }
}
.section-titles.active {
  opacity: 1;
}
.section-text {
  font-size: 16px;
  margin: 0 0 32px 0;
}
@media screen and (max-width: 767px) {
  .section-text {
    font-size: 14px;
  }
}
.section-list {
  padding: 0 0 0 40px;
  margin: 0 0 32px 0;
  border-left: 1px solid #BBC0C9;
  font-size: 16px;
}
@media screen and (max-width: 767px) {
  .section-list {
    padding: 0 0 0 20px;
    font-size: 14px;
  }
}
.section-detail_title {
  font-size: 24px;
  margin: 0 0 24px 0;
  font-weight: bold;
}
@media screen and (max-width: 767px) {
  .section-detail_title {
    font-size: 16px;
  }
}
.section-detail_text {
  font-size: 16px;
  padding: 0 0 8px 40px;
  border-left: 1px solid #BBC0C9;
}
@media screen and (max-width: 767px) {
  .section-detail_text {
    font-size: 14px;
    padding: 0 0 8px 20px;
  }
}
.section-thumbs {
  position: relative;
  top: 0;
  overflow: hidden;
}
.section-thumbs::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  transition: left 0.8s ease 0s, width 1s ease 0.5s;
}
.section-thumbs.active::before {
  width: 0%;
  left: 100%;
}

.business {
  max-width: 1280px;
  margin: 0 auto 156px;
}
