.footer {
  border-top: 1px solid #0F131C;
  border-bottom: 1px solid #0F131C;
  background: #fff;
}
.footer-inner {
  margin: 0 40px;
  border-right: 1px solid #0F131C;
  border-left: 1px solid #0F131C;
}
@media screen and (max-width: 767px) {
  .footer-inner {
    margin: 0 20px;
  }
}
.footer-column {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  border-bottom: 1px solid #0F131C;
}
@media screen and (max-width: 767px) {
  .footer-column {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, auto);
  }
}
.footer-column:last-child {
  border-bottom: none;
}
.footer-company {
  padding: 32px 0 56px 32px;
  border-right: 1px solid #0F131C;
}
@media screen and (max-width: 767px) {
  .footer-company {
    padding: 32px 20px 56px 20px;
    border-right: none;
    border-bottom: 1px solid #0F131C;
  }
}
.footer-maps {
  position: relative;
  top: 0;
}
@media screen and (max-width: 767px) {
